import { Component } from '@angular/core';
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  LoadingController,
  Loading
} from 'ionic-angular'; //sicredisms.com
import { AnalisisProvider } from '../../providers/analisis/analisis';

/**
 * Generated class for the ResultadosDetallePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'ResultadosDetallePage'
})
@Component({
  selector: 'page-resultados-detalle',
  templateUrl: 'resultados-detalle.html'
})
export class ResultadosDetallePage {
  resultado: any;
  id: string = '0';
  url: string = '';
  resultadoVT: any;
  loading: Loading;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private analisisPrv: AnalisisProvider,
    private viewCtrl: ViewController,
    private loadingCtrl: LoadingController
  ) {
    this.resultado = this.navParams.get('resultados');
    this.id = this.navParams.get('id');
    this.url = this.navParams.get('url');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ResultadosDetallePage');
    this.obtenerVirusTotal();
  }

  obtenerVirusTotal() {
    this.showLoader();
    this.analisisPrv.getVirusTotal(Number(this.id)).subscribe(
      data => {
        this.loading.dismiss();
        console.log(data.json());
        this.resultadoVT = data.json();
      },
      error => {
        this.loading.dismiss();
      }
    );
  }

  obtenerResultado(resultado: number): string {
    if (resultado == 0) {
      return 'Sitio potencialmente peligroso.';
    } else {
      return 'Sitio inofensivo';
    }
  }
  salir() {
    this.viewCtrl.dismiss();
  }

  showLoader() {
    this.loading = this.loadingCtrl.create({
      content: 'Consultando a VirusTotal...'
    });
    this.loading.present();
  }
}



